<template>
    <div>
        <div class="func-header">
            <div class="container">
                <div class="func-label">
                    <h1 style=""></h1>
                    <div>80QQ</div>
                    <span class="kaitong"></span>
                </div>
                <div class="pull-right signin"><a href="#"><img src="../../assets/signin_svip8.png"></a></div>
                <div class="mod-function-list">
                    <mode-function-item v-for="item in modeLists" :item="item" :key="item.id"></mode-function-item>
                </div>
            </div>
        </div>
        <div class="container">
            <div class="mainfunc">
                    <ul class="vipfuncs">
                        <li v-for="(vip1,index) in vipfunc" :key="index" :class="{on: index===currIndex}" @click="currIndex=index"><router-link to="#">{{vip1.title}}</router-link></li>
                    </ul>
                    <div class="vipfuncs-container">
                        <template  v-for="(vip2,index) in vipfunc">
                            <ul :class="[vip2.css,{hide: index !==currIndex}]">
                                <li v-for="data in vip2.contains"><a href="#"><span></span><h3>{{data}}</h3></a></li>
                            </ul>
                        </template>
                    </div>
                    <router-view></router-view>
            </div>
        </div>
    </div>
</template>
<script>
    import modeFunctionItem from '../plugins/modeFunctionItem'
    export default {
        components: {
            modeFunctionItem
        },
        data(){
            return {
                currIndex: 0,
                bgimg: require('../../assets/function_banner.png'),
                modeLists: [
                    {
                        "title": "2000",
                        "desc": " "
                        },
                        {
                        "title": "",
                        "desc": ""
                        },
                        {
                        "title": "QQ",
                        "desc": "QQ"
                        },
                        {
                        "title": "",
                        "desc": " "
                        },
                        {
                        "title": "",
                        "desc": ""
                        }
                ],
                vipfunc: [
                    {
                        title: "",
                        css: "red",
                        contains: ["","","", "", "", "","", ""]
                    },
                    {
                        title: "",
                        css: "green",
                        contains: ["", "", "", "", "", "@", "2000","", "", "",""]
                    },
                    {
                        title: "",
                        css: "yellow",
                        contains: ["", "Q/Q9.1", "", "",""]
                    },
                    {
                        title: "",
                        css:"zi",
                        contains: ["", "", "", "","", "QQ", "","", "", "", ""]
                    },
                    {
                        title: "",
                        css: "lv",
                        contains: ["VIP", "", "","", "", "",""]
                    },
                    {
                        title: "",
                        css: "lan",
                        contains: ["", "", "", "", "", ""]
                    },
                ]
            }
        },
        mounted(){
            // this.$http.get("api/modelist").then((data)=>{
            //     this.modeLists = data.data;
            // })
        }
    }
</script>
<style>
    .func{
        /* margin-left: -34%;
        margin-top: -10px;
        margin-right: -33.5%; */
    }
    .func-header{
        /* background: url('../../assets/function_banner.png') no-repeat; */
        /* background: url('../../../static/function_banner.png') no-repeat; */
        max-width: 100%;
        min-height: 320px;
        color: white;
        padding-top: 20px;
    }
    .func-header .func-label{
        /* margin-top: 10px; */
        text-align: center;
    }
    
    .kaitong{
        background-color: goldenrod;
        margin-top: 20px;
        padding: 16px;
        font-size: 20px;
        font-weight: 500;
        display: inline-block;
        border-radius: 40px;
        transition: all 1s;
    }
    .kaitong:hover{
        /* color: ; */
        cursor: pointer;
        background-color: gold;
    }
    .signin{
        position: fixed;
        top: 130px;
        right: -80px;
        transition: all .5s;
    }
    .signin:hover{
        right: 0;
    }
    .mod-function-list{
        position: relative;
        /* width: 50%; */
        margin:auto;
        padding: 15px;
        vertical-align: top;
        text-align: center;
    }
    .mainfunc{
        margin-top:30px;
    }
    .vipfuncs{
        margin: 0;
        padding: 0;
        list-style-type: none;
        border-bottom: 1px solid #ccc;
    }
    .vipfuncs li{
        box-sizing: border-box;
        display: inline-block;
        font-size: 16px;
        margin-bottom: -1px;
        cursor: pointer;
        
    }
    .vipfuncs .on{
        border-bottom: 4px solid gold;
    }
    .vipfuncs .on a{
        color:#444; 
    }
    .vipfuncs li a{
        color: #777;
        text-decoration: none;
        padding: 0 10px;
        line-height: 42px;
    }
    .vipfuncs-container ul{
        list-style-type: none; 
        margin-top: 5px;
        padding: 10px 0;
        /* position: relative; */
    }
    .vipfuncs-container ul li{
        box-sizing: border-box;
        display: inline-block;
        /* float: left; */
        /* margin-top: 10px; */
        /* margin-right: 0.5%; */
        width: 14%;
        height: 240px;;
        text-align: center;
        border: solid 1px #fbfbfb;
    }
    .vipfuncs-container ul li:hover{
        border: 1px solid #ccc;
        /* cursor: pointer; */
    }
    .vipfuncs-container ul li a{
        display: block;
        width: 100%;
        height: 100%;
    }
    .vipfuncs-container li a{
        text-decoration: none;
        color: #444;
    }
    .vipfuncs-container li span{
        display: block;
        margin: 30px auto;
        width: 80%;
        height: 50%;
        background-color: goldenrod;
        border-radius: 1000px;
        /* background-color: pink;
        width: 50%;
        height: 50%;
        border-radius: 100px;
        /* border: 1px solid pink; */
        /* margin-top:  auto; */
        /* width: 100%; */
        /* vertical-align: center; */
        /* font-size: 13px; */
    }
    .vipfuncs-container ul.red li span{
        background-color: #d30000;
    }
    .vipfuncs-container ul.green li span{
        background-color: #2ada86;
    }
    .vipfuncs-container ul.yellow li span{
        background-color: goldenrod;
    }
    .vipfuncs-container ul.zi li span{
        background-color:#9964f1;
    }
    .vipfuncs-container ul.lv li span{
        background-color:#0ad1ae;
    }
    .vipfuncs-container ul.lan li span{
        background-color:#03afff;
    }
    .vipfuncs-container li h3{
        font-size: 13px;
    }
</style>




